<template>
  <v-app>
    <header-tool-bar :name="name" :sign-out="signOut" :drawer="drawer" @toggle-drawer="drawer = !drawer" />
    <v-layout>
      <menus ref="menu" v-model:drawer="drawer"></menus>
      <v-main class="dashboard">
        <router-view />
      </v-main>
    </v-layout>
  </v-app>
</template>

<script setup>
import { auth } from "@/helpers/auth";
import menus from "@/components/menus";
import { my } from "@/requests";
import headerToolBar from "@/components/headerToolBar.vue";

const route = useRoute();

const name = ref(null);
const pictureUrl = ref("/images/!logged-user.jpg");
const { signOut, refreshToken, user } = auth();
const menu = ref(null);
const drawer = ref(true);
onMounted(async () => {
  refreshToken();
  name.value = user().nickname;
});


provide("menu", menu);
</script>